import React from 'react';
import stateForm from './stateFormObject';
class Form extends React.Component {
    constructor(props) {
		super(props)

		this.state = {
			description: '',
			deadline: ''			
		}   
    };
    handleDescriptionChange = event => {
		this.setState({
			description: event.target.value
		})
    }
    handleDeadlineChange = event => {
		this.setState({
			deadline: event.target.value
        })
        setTimeout(() => {
            if(this.state.description)
            stateForm[0] = this.state;
        }, 500);
        if(this.state.description)
            stateForm[0] = this.state;
	}
    render(){
        const {id}= this.props;
        const {description, deadline} = this.state;
        return(
            <form onSubmit = {this.props.addTodo.bind(this, id)}>
                <div>
                    <label>Todo description</label>
                    <input type='text' name = 'description' value = {description} onChange = {this.handleDescriptionChange} />
                </div>
                <div>
                    <label>deadline</label>
                    <input type='date' name = 'deadline' value = {deadline} onChange = {this.handleDeadlineChange}/>
                </div>
            </form>
        )
    }    
} 

export default Form;